.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
@font-face {
  src: url("./Components/STJEDISE.TTF");
  font-family: STJEDISE;
}
.App {
  background: black;
  background-image: url("./Components/star-wars-background1.jpg");
  background-position: right;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;

  min-height: 100vh;
  /* height: 100vh; */
  scroll-behavior: smooth;
}
/*-------------------------------------------------heading part---------------*/
.top-most-heading-div {
  text-align: center;
}

.top-most-heading-div .top-most-heading {
  margin: 0;
  border: 0;
  fill: #a03131;
  font-family: STJEDISE;
  stroke: 1px yellow;
  color: yellow;
  font-size: 50px;
}

.top-most-heading-div p {
  margin: 0;
  border: 0;
  color: white;
}
.homepage-characters-button {
  margin-left: 30px;
}

.characters-button {
  background-image: url("./Components/star-wars-characters1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border: 0;
  height: 400px;
  width: 300px;
  color: white;
}
.characters-name {
  color: white;
  font-size: x-large;
}
